<template>
    <div>
        <!-- Âè≥ÈîÆËèúÂçï -->
        <ul id="rightMenu" style="width: 100px">
            <li @click="handleMenu('notify')">ÈÄöÁü•</li>
            <li @click="handleMenu('close')">ÂÖ≥Èó≠</li>
        </ul>
    </div>
</template>

<script>
export default {
    mounted() {
        this.initMenu();
    },
    beforeDestroy() {
        this.closeMenu()
    },
    destroyed() {
        this.closeMenu()
    },
    methods: {
        handleMenu(command) {
            switch (command) {
                case 'notify':
                    this.$notify({
                        title: 'Ê∂àÊÅØ',
                        message: 'ÈÄöÁü•ü™£',
                        type: 'success'
                    });
                    this.notifyCoder()
                    break;
                case 'close':
                    this.$notify.info({
                        title: 'Ê∂àÊÅØ',
                        message: 'ÂÖ≥‰∏™ÁêÉÁêÉ‚öΩÔ∏è'
                    });
                    this.closeMenu()
                    break;
            }
        },
        initMenu() {
            //ÊèêÂèñÂà∞ÂáΩÊï∞Â§ñÈù¢‰Ωú‰∏∫ÂÖ®Â±ÄÂèòÈáè
            var menu = document.getElementById("rightMenu");

            //Ëá™ÂÆö‰πâ‰∏Ä‰∏™ÊµèËßàÂô®Âè≥ÈîÆËèúÂçïÔºåÂçïÂáªÂè≥ÈîÆÊòØÊòæÁ§∫ÂÆÉ
            //oncontextmenu‰∏ä‰∏ãÊñáËèúÂçï‰∫ã‰ª∂ÔºåÂè≥ÈîÆËèúÂçï
            document.documentElement.oncontextmenu = function (event) {
                //ÊòæÁ§∫Êàë‰ª¨Ëá™Â∑±ÂÆö‰πâÁöÑÂè≥ÈîÆËèúÂçï
                //1.ÊâæÂà∞ËèúÂçï
                //ÊèêÂèñÂà∞ÂáΩÊï∞Â§ñÈù¢‰Ωú‰∏∫ÂÖ®Â±ÄÂèòÈáè

                //ÂÖºÂÆπEventÂØπË±°
                event = event || window.event;

                //2.ËÆæÁΩÆËèúÂçïÁöÑ‰ΩçÁΩÆÁ≠â‰∫éÈº†Ê†áÁöÑÂùêÊ†á
                //Âà§Êñ≠ÔºöÂ¶ÇÊûúÈº†Ê†áÁöÑ‰ΩçÁΩÆ+ËèúÂçïÁöÑÂÆΩÂ∫¶>ÁΩëÈ°µÁöÑÂÆΩÂ∫¶ÔºåÈÇ£‰πàÂ∞±Êîπ‰∏∫Âè≥ËæπÂÆö‰Ωç
                //Èº†Ê†áÂùêÊ†á
                var mx = event.clientX;
                var my = event.clientY;
                //ËèúÂçïÂÆΩÂ∫¶
                var menuWidth = parseInt(menu.style.width);
                //ÁΩëÈ°µÁöÑÂÆΩÂ∫¶(È´òÂ∫¶Áî®ÂêåÊ†∑ÁöÑÊñπÊ≥ïËß£ÂÜ≥)
                var pageWidth = document.documentElement.clientWidth;
                //console.log(pageWidth);
                if (mx + menuWidth < pageWidth) {
                    menu.style.left = mx + "px";
                    menu.style.top = my + "px";
                } else {
                    menu.style.right = mx + "px";
                    menu.style.top = my + "px";
                }

                //3.ÊòæÁ§∫Âè≥ÈîÆËèúÂçï
                menu.style.display = "block";
                // alert('display: block');

                //ÈòªÊ≠¢ÈªòËÆ§ÁöÑÂè≥ÈîÆËèúÂçïÊòæÁ§∫
                return false;
            };

            //‰ªª‰ΩïÁÇπÂáª ÈöêËóèËèúÂçï
            document.documentElement.onclick = function () {
                menu.style.display = "none";
            };
        },
        closeMenu() { 
            document.documentElement.oncontextmenu = function () {
                // ÂÖÅËÆ∏Âè≥ÈîÆÊòæÁ§∫
                return true;
            }
            setTimeout(() => {
                this.initMenu();
            },1000*3)
        },
        lockMenu() {
            
            //ÊÄªÊòØÊòæÁ§∫ËèúÂçï
            document.documentElement.onclick = function () {
                menu.style.display = "display";
            };
        },
        notifyCoder() {
            
        }
    }
};
</script>
